.top {
	position: fixed;
	display: none;
	width: 44px;
	height: 44px;
	text-align: center;
	line-height: 34px;
	font-size: 16px;
	bottom: 25px;
	right: 25px;
	border: none;
	outline: none;
	z-index: 10000;
	opacity: 0;
	@include transition();

	&.show {
		display: inline;
		opacity: 1;
	}
}
